


*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.h1,h2,h3,h4,h5,h6 {
  margin: 0;
  padding: 0;
}

// ==== MIXIN ==== //

@mixin panel {
  background-color: $color-panels;
  border-radius: 4px;
  box-shadow: $shadow-panels;
}

@mixin quicksand {
  font-family: 'Quicksand', sans-serif;
}

@mixin poppins {
  font-family: 'Poppins', sans-serif;
}

// ==== // MIXIN ==== //



// ==== Variables ====

$color-fon: #edeef0;
$color-panels: #ffff;
$color-text: #6f7985;

$shadow-panels:  0 1px 0 0 #dce1e6, 0 0 0 1px #e7eBec;

$fw-bold: 700;
$fw-light: 300;
$fw-medium: 400;

// ==== // Variables ====


.mainPage {
  @include quicksand;
  background-color: $color-fon;
  // padding: 30px 20px;
  min-height: 100vh;
  &_inner {
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px 40px;
    display: flex;
  }
}

.navigation {
  @include panel;
  margin-right: 20px;
  height: 320px;
  min-width: 200px;
  color: $color-text;
  ul {
    padding: 10px 20px;
    list-style: none;
    li {
      cursor: pointer;
      border-radius: 4px;
      font-size: 22px;
      margin: 10px 0px;
      padding: 5px;
      &:hover {
        background-color: $color-fon;
      }
    }
  }
  &_icon {
    text-align: center;
    &_git {
      width: 50px;
      opacity: .7;
      transition: all .1s;
      &:hover {
        opacity: 1;
      }
    }
  }
}

.link {
  color: $color-text;
  text-decoration: none;
  display: block;
  width: 100%;
}

.content {
  flex-grow: 1;
  margin-right: 20px;
  &_title {
    @include panel;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    &_text {
      padding: 20px 50px
    }
  }
  &_works {
    // @include panel;
    margin-bottom: 20px;
    height: 340px;
  }
  &_technologies {
    @include panel;
    &_inner {
      padding: 0px 50px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      &_item {
        width: 80px;
        height: 80px;
        cursor: pointer;
        margin: 10px 15px;
        opacity: .7;
        @media (max-width: 1180px) {
          width: 40px;
          height: 40px;
        }
        &:hover {
          opacity: 1;
        }
        &_pic {
          display: block;
          width: 100%;
          height: auto;
        }
      }
    }
    &_title {
      padding-left: 50px;
      padding-top: 20px;
      padding-bottom: 20px;
      border-bottom: 1px solid $color-fon
    }
  }
}


.profile {
  @include panel;
  text-align: center;
  max-height: 250px;
  color: $color-text;
  &_inner {
    padding: 10px 10px;
  }
  &_image {
    margin: 0 auto;
    margin-bottom: 18px;
    cursor: pointer;
    &_pic {
      display: block;
      width: 100%;
      height: auto;
      border-radius: 50%;
    }
  }
  &_name {
    font-size: 18px;
    font-weight: $fw-bold;
  }
  &_age {
    font-size: 14px;
    font-weight: $fw-medium;
    margin-top: 10px;
    margin-bottom: 5px;
  }
  &_city {
    font-size: 14px;
    font-weight: $fw-medium;
  }
}

// === Swiper === // 


.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-pagination {
  position: absolute;
  text-align: center;
  transition: .3s opacity;
  transform: translate3d(0,0,0);
  z-index: 10;
}

.swiper-pagination-bullets {
  bottom: 10px;
  left: 0;
  width: 100%;
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  background: #000;
  margin: 0 4px;
  background-color: $color-text;
}

.swiper-pagination-bullet-active {
  background-color: rgb(13, 143, 30)
}




// === // Swiper === // 


.experience {
  @include panel;
  margin-bottom: 20px;
}

.work {
  padding: 20px 50px;
  &-title {
    display: flex;
    align-items: center;
    padding-bottom: 30px;
    border-bottom: 1px solid #edeef0;
    &-logo {
      width: 150px;
      height: 150px;
      margin-right: 30px;
      display: block;
      height: auto;
      background-color: #040720;
      padding: 20px 20px;
      border-radius: 10px;
      opacity: .9;
      &:hover {
        opacity: 1;
      }
    }
    &-text {
      font-size: 20px;
      font-weight: $fw-medium;
      
      h4 {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
    }
  }
}
}

.work-title-logo--fiveLb {
  width: 100px;
  background-color: #fff;
  padding: 0;
}

.work-content-images {
  justify-content: center;
  display: flex;
  &-wrapper {
    margin: 0 auto;
  }
  &-item {
    width: 200px;
    height: auto;
    border-radius: 5px;
    margin-right: 40px;
    &:last-child {
      margin-right: 0px;
    }
  }
}

.about {
  @include panel;
  font-weight: $fw-medium;
  font-size: 22px;
  &_inner {
    padding: 20px 50px;
  }
}



// === Slides === //

.slide {
  background-position: center;
  background-size: cover;
  border-radius: 25px;
  position: relative;
  &--arithmetic {
    background-image: url('./images/promo/math.png');
  }
  &--auth {
    background-image: url('./images/promo/auth1.png');
  }
  &--simple {
    background-image: url('./images/promo/simple1.png');
  }
  &--jsonData {
    background-image: url('./images/promo/data1.png');
  }
}

.swiperLink {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.slide_title {
  color: #fff;
  position: absolute;
  top: 120px;
  left: 133px;
  z-index: 2;
  @include poppins;
  font-size: 36px;
  max-width: 210px;
  line-height: 35px;
  &--simple {
    top: 120px;
    left: 100px;
  }
  &--jsonData {
    top: 100px;
    padding: 30px 20px;
    background-color: rgba(0, 0, 0, 0.63);
    border-radius: 25px;
    max-width: 310px;
  }
}

.slide_info {
  color: #fff;
  position: absolute;
  bottom: 40px;
  right: 40px;
  z-index: 2;
  font-size: 24px;
  font-weight: $fw-bold;
}








